import * as React from "react";
import "./tabs.scss";
export class Tabs extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selected: 0
        };
    }
    static getChildrenOfPropsType(children, childType) {
        return React.Children.map(children, function (child) {
            const type = (child && child["type"]) || null;
            if (type && type === childType)
                return child;
            return null;
        }).filter(function (a) {
            return !!a;
        });
    }
    static getTabs(children) {
        var tabChildren = Tabs.getChildrenOfPropsType(children, Tab);
        return tabChildren.map(function (el) {
            const children = el.props && el.props.children;
            if (!children)
                return null;
            const tabModel = {
                button: null,
                view: null
            };
            const tabButtonChildren = Tabs.getChildrenOfPropsType(children, TabButton);
            if (tabButtonChildren && tabButtonChildren.length)
                tabModel.button = tabButtonChildren[0];
            const tabViewChildren = Tabs.getChildrenOfPropsType(children, TabView);
            if (tabViewChildren && tabViewChildren.length)
                tabModel.view = tabViewChildren[0];
            return tabModel;
        }).filter(function (tabModel) {
            return !!tabModel && !!tabModel.button && !!tabModel.view;
        });
    }
    selectTab(index) {
        this.setState({
            selected: index
        });
    }
    render() {
        const tabs = Tabs.getTabs(this.props.children);
        const { selected } = this.state;
        const tabTitles = tabs.map((tab, index) => {
            const className = ["tab-button"];
            let onClick = null;
            if (index === selected) {
                className.push("tab-active");
            }
            else {
                onClick = this.selectTab.bind(this, index);
            }
            return (React.createElement("div", { key: index, className: className.join(" "), onClick: onClick }, tab.button.props.children));
        });
        const activeTabContent = tabs.filter(function (tab, index) {
            return index === selected;
        })[0];
        const buttons = React.createElement("div", { className: "tab-buttons" }, tabTitles);
        const views = React.createElement("div", { className: "tab-view" }, activeTabContent.view.props.children);
        let content = null;
        content =
            React.createElement(React.Fragment, null,
                views,
                buttons);
        return (React.createElement("div", { className: "react-tabs" }, content));
    }
}
export class Tab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {};
    }
    render() {
        return (React.createElement("div", { className: "react-tabs-tab" }, this.props.children));
    }
}
export class TabButton extends React.PureComponent {
    render() {
        return (React.createElement("div", { className: "react-tabs-tab-button" }, this.props.children));
    }
}
export class TabView extends React.PureComponent {
    render() {
        return (React.createElement("div", { className: "react-tabs-tab-view" }, this.props.children));
    }
}



