import * as React from "react";
export class StyleScript extends React.Component {
    constructor(props) {
        super(props);
        this.ref = null;
        this.ref = React.createRef();
    }
    render() {
        return React.createElement("div", { ref: this.ref, style: { position: "absolute", width: "100%", height: this.props.input } });
    }
    componentDidMount() {
        let outputPixels = -1;
        if (this.ref && this.ref.current)
            outputPixels = this.ref.current.offsetHeight;
        this.props.outputPixels(outputPixels);
    }
}



