import * as React from "react";
import "./more.scss";
import * as Noaa from "../../services/noaa";
import * as Time from "../../services/time";
export class More extends React.Component {
    componentDidMount() {
        const { noaa } = this.props;
        if (!noaa.data && !noaa.isRequesting) {
            Noaa.getNoaaData()
                .then((data) => {
                noaa.onRequestEnd(data);
            });
            noaa.onRequestBegin();
        }
    }
    componentWillUnmount() {
        const { noaa } = this.props;
        if (!noaa.data && noaa.isRequesting)
            this.props.noaa.onRequestEnd(null);
    }
    render() {
        const { noaa } = this.props;
        if ((!noaa.data && !noaa.isRequesting) || (noaa.data && noaa.data.errors) || (noaa.data && noaa.data.data && !noaa.data.data.current)) {
            return React.createElement("p", null, "Error....");
        }
        else if (!noaa.data && noaa.isRequesting) {
            return React.createElement("p", null, "Requesting...");
        }
        else {
            const current = noaa.data.data.current;
            const prettyTime = Time.createPrettyTime(current.airTemp.time);
            return (React.createElement("div", { className: "more tab-view-bg" },
                React.createElement("header", null,
                    React.createElement("div", { className: "title" }, "Current Conditions"),
                    React.createElement("div", { className: "timing" },
                        "as of ",
                        React.createElement("span", { className: "pretty-time" }, prettyTime.time),
                        React.createElement("span", { className: "pretty-ampm" }, prettyTime.ampm))),
                React.createElement(DataSection, { title: "Water Temperature", value: current.waterTemp.val, unit: "Degrees (F)" }),
                React.createElement(DataSection, { title: "Air Temperature", value: current.airTemp.val, unit: "Degrees (F)" }),
                React.createElement(DataSection, { title: "Air Pressure", value: current.airPressure.val, unit: "Millibars (mb)" }),
                React.createElement("section", null,
                    React.createElement("div", { className: "data-title" }, "Wind"),
                    React.createElement("div", { className: "data-value" },
                        React.createElement("span", { className: "value" }, current.wind.speed),
                        React.createElement("span", { className: "unit" },
                            "knots ",
                            current.wind.directionCardinal)),
                    React.createElement("div", { className: "data-value" },
                        React.createElement("span", { className: "value" }, current.wind.gust),
                        React.createElement("span", { className: "unit" }, "knot gusts")))));
        }
    }
}
class DataSection extends React.Component {
    render() {
        const props = this.props;
        return (React.createElement("section", null,
            React.createElement("div", { className: "data-title" }, props.title),
            React.createElement("div", { className: "data-value" },
                React.createElement("span", { className: "value" }, props.value),
                React.createElement("span", { className: "unit" }, props.unit))));
    }
}



