// These definitions are set in your webpack config.
// Will fail if not set proerly in the webpack config.
export const DEFINE = {
    BUILD: {
        IS_PRODUCTION: webpack.BUILD.IS_PRODUCTION,
        VERSION: webpack.BUILD.VERSION,
        TIME: webpack.BUILD.TIME
    },
    DEBUG: {
        LOCAL_REQUEST_DATA: webpack.DEBUG.LOCAL_REQUEST_DATA
    }
};
// Make these public on the window for us to easily check
window["DEFINE"] = DEFINE;



