import * as React from "react";
import * as Noaa from "../../services/noaa";
import { Title } from "./title/title";
import { Wave } from "./wave/wave";
import "./tide.scss";
export class Tide extends React.Component {
    constructor(props) {
        super(props);
        this.clickShare = () => {
        };
        this.clickRefresh = () => {
        };
        this.state = {};
    }
    componentDidMount() {
        const { noaa } = this.props;
        if (!noaa.data && !noaa.isRequesting) {
            Noaa.getNoaaData()
                .then((data) => {
                noaa.onRequestEnd(data);
            });
            noaa.onRequestBegin();
        }
    }
    componentWillUnmount() {
        const { noaa } = this.props;
        if (!noaa.data && noaa.isRequesting)
            this.props.noaa.onRequestEnd(null);
    }
    render() {
        const { noaa } = this.props;
        if ((!noaa.data && !noaa.isRequesting) || (noaa.data && noaa.data.errors)) {
            return React.createElement("p", null, "Error....");
        }
        else if (!noaa.data && noaa.isRequesting) {
            return React.createElement("p", null, "Requesting...");
        }
        else {
            const data = noaa.data;
            return (React.createElement("div", { className: "tide" },
                React.createElement(Title, { noaaResponse: data }),
                React.createElement(Wave, { noaaResponse: data })));
        }
    }
}



